// write.js
import {
  collection,
  getDocs,
  addDoc,
  updateDoc,
  doc,
  deleteDoc,
} from "firebase/firestore";

// Actions
const LOAD = "write/LOAD";
const ADD = "write/ADD";

const initialState = {
  list: [
    {
      todo: " !",
      checkComplete: "false",
      goalDay: "220616",
    },
    {
      todo: "   ",
      checkComplete: "true",
      goalDay: "220616",
    },
    
  ],
};

// Action Creators
export function loadWrite(write_list) {
  return { type: LOAD, write_list };
}

export function addWrite(write) {
  return { type: ADD, write };
}

